(76, 175, 80, 0.70);\n  }\n  &.waves-teal .waves-ripple {\n    background-color: rgba(0, 150, 136, 0.70);\n  }\n\n  // Style input button bug.\n  input[type=\"button\"], input[type=\"reset\"], input[type=\"submit\"] {\n    border: 0;\n    font-style: normal;\n    font-size: inherit;\n    text-transform: inherit;\n    background: none;\n  }\n\n  img {\n    position: relative;\n    z-index: -1;\n  }\n}\n\n.waves-notransition {\n  transition: none #{\"!important\"};\n}\n\n.waves-circle {\n  transform: translateZ(0);\n  -webkit-mask-image: -webkit-radial-gradient(circle, white 100%, black 100%);\n}\n\n.waves-input-wrapper {\n  border-radius: 0.2em;\n  vertical-align: bottom;\n\n  .waves-button-input {\n    position: relative;\n    top: 0;\n    left: 0;\n    z-index: 1;\n  }\n}\n\n.waves-circle {\n  text-align: center;\n  width: 2.5em;\n  height: 2.5em;\n  line-height: 2.5em;\n  border-radius: 50%;\n  -webkit-mask-image: none;\n}\n\n.waves-block {\n  display: block;\n}\n\n/* Firefox Bug: link not triggered */\n.waves-effect .waves-ripple {\n  z-index: -1;\n}",".modal {\n  @extend .z-depth-4;\n\n  display: none;\n  position: fixed;\n  left: 0;\n  right: 0;\n  background-color: #fafafa;\n  padding: 0;\n  max-height: 70%;\n  width: 55%;\n  margin: auto;\n  overflow-y: auto;\n\n  border-radius: 2px;\n  will-change: top, opacity;\n\n  @media #{$medium-and-down} {\n   width: 80%;\n  }\n\n  h1,h2,h3,h4 {\n    margin-top: 0;\n  }\n\n  .modal-content {\n    padding: 24px;\n  }\n  .modal-close {\n    cursor: pointer;\n  }\n\n  .modal-footer {\n    border-radius: 0 0 2px 2px;\n    background-color: #fafafa;\n    padding: 4px 6px;\n    height: 56px;\n    width: 100%;\n    text-align: right;\n\n    .btn, .btn-flat {\n      margin: 6px 0;\n    }\n  }\n}\n.modal-overlay {\n  position: fixed;\n  z-index: 999;\n  top: -25%;\n  left: 0;\n  bottom: 0;\n  right: 0;\n  height: 125%;\n  width: 100%;\n  background: #000;\n  display: none;\n\n  will-change: opacity;\n}\n\n// Modal with fixed ac